export var Income;
(function (Income) {
    Income.Enum = {
        REGULAR_INCOME: {
            name: 'REGULAR_INCOME',
            desc: '정기 소득',
        },
        IRREGULAR_INCOME: {
            name: 'IRREGULAR_INCOME',
            desc: '비정기 소득',
        },
        ANNUITY: {
            name: 'ANNUITY',
            desc: '연금',
        },
        ETC: {
            name: 'ETC',
            desc: '기타수입',
        },
    };
    Income.values = () => {
        return Object.values(Income.Enum);
    };
    Income.findBy = (name) => {
        return Income.Enum[name] ?? {};
    };
    Income.of = (key) => {
        return Income.Enum[key] ? Income.Enum[key].name : '';
    };
})(Income || (Income = {}));
