import httpClient from './http';
import { logger } from 'utils';
import { dispatch } from 'store';
import { setJwtExpired } from 'store/slices/auth';

// ---------------------------------------------------------------------- //
type jwtTypes = { iat: number; exp: number; id: string };
// ---------------------------------------------------------------------- //

export const accessTokenKey = 'fa-access-token';

export const jwtDecode = (token: string): jwtTypes | null => {
  try {
    const base64Url = token.split('.')[1];
    const base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
    const jsonPayload = decodeURIComponent(
      window
        .atob(base64)
        .split('')
        .map((c) => `%${`00${c.charCodeAt(0).toString(16)}`.slice(-2)}`)
        .join('')
    );

    const decodedToken = JSON.parse(jsonPayload) as jwtTypes;
    return decodedToken;
  } catch (error) {
    logger.error('[jwtDecode] ', error);
    return null;
  }
};

// ---------------------------------------------------------------------- //
export const getAccessToken = () =>
  typeof window !== 'undefined' ? localStorage.getItem(accessTokenKey) : '';

// ---------------------------------------------------------------------- //
// test
export const isValidToken = (accessToken: string) => {
  if (!accessToken) {
    return false;
  }

  const decoded = jwtDecode(accessToken);
  if (!decoded?.exp) return false;

  const currentTime = Date.now() / 1000;

  return decoded.exp > currentTime;
};

// ---------------------------------------------------------------------- //

export const setSession = (accessToken: string | null) => {
  if (accessToken) {
    localStorage.setItem(accessTokenKey, accessToken);
    httpClient.defaults.headers.common.Authorization = `Bearer ${accessToken}`;
  } else {
    localStorage.removeItem(accessTokenKey);
    delete httpClient.defaults.headers.common.Authorization;
    dispatch(setJwtExpired(true));
  }
};
