import { configureStore, AnyAction, Store, Reducer } from '@reduxjs/toolkit';
import {
  persistStore,
  persistReducer,
  FLUSH,
  REHYDRATE,
  PAUSE,
  PERSIST,
  PURGE,
  REGISTER
} from 'redux-persist';
import persistantStorage from 'redux-persist/lib/storage';
import rootReducer, { RootState as ReduxRootState } from './rootReducer';
import {
  TypedUseSelectorHook,
  useDispatch as useReduxDispatch,
  useSelector as useReduxSelector
} from 'react-redux';

// ---------------------------------------------------------------------- //

const persistConfig = {
  key: 'admin',
  keyPrefix: '',
  storage: persistantStorage,
  whitelist: ['customer']
};

interface CustomStore extends Store<ReduxRootState, AnyAction> {}

const store: CustomStore = configureStore({
  reducer: persistReducer(persistConfig, rootReducer as Reducer),
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware({
      immutableCheck: false,
      serializableCheck: {
        ignoredActions: [FLUSH, REHYDRATE, PAUSE, PERSIST, PURGE, REGISTER]
      }
    }),
  devTools: import.meta.env.DEV
});

const persistor = persistStore(store);

export type RootState = ReturnType<typeof store.getState>;
export type AppDispatch = typeof store.dispatch;

const { dispatch } = store;

const useSelector: TypedUseSelectorHook<RootState> = useReduxSelector;
const useDispatch = () => useReduxDispatch<AppDispatch>();

export { store, persistor, dispatch, useSelector, useDispatch };
