import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import _isArray from 'lodash/isArray';
import _cloneDeep from 'lodash/cloneDeep';
import { ADVISOR_NAV, CLIENT_NAV } from 'config/gnb';
import { User } from '@package/shared-consts';

// ----------------------------------------------------------------------

export type GnbState = {
  advisorNav: Gnb.NavItem[];
  clientNav: Gnb.NavItem[];
  activeSubmenu: Gnb.NavItem;
};

function filterNavItems(nav: Gnb.NavItem[] | Gnb.NavItemBase[], userRole: string) {
  return nav?.filter((item) => {
    const _navItem = _cloneDeep(item);
    if (_navItem.roles && _navItem.roles.includes(userRole)) {
      if (_isArray(_navItem.children)) {
        _navItem.children = filterNavItems(_navItem.children, userRole);
      }
      return true;
    }
    return false;
  }) as Gnb.NavItem[];
}

