const initialState: GnbState = {
  advisorNav: ADVISOR_NAV, // sortNavItems(ADVISOR_NAV),
  clientNav: CLIENT_NAV, // sortNavItems(CLIENT_NAV),
  activeSubmenu: {} as Gnb.NavItem
};

export const gnbSlice = createSlice({
  name: 'gnb',
  initialState,
  reducers: {
    setActiveSubmenu: (state, action: PayloadAction<{ itemKey: string; mode: Gnb.NavMode }>) => {
      const _nav = action.payload.mode === 'advisor' ? ADVISOR_NAV : CLIENT_NAV;
      const foundActiveSubmenu = _nav.find(({ id }) => id === action.payload.itemKey);
      state.activeSubmenu = foundActiveSubmenu ? foundActiveSubmenu : initialState.activeSubmenu;
    },
    setNavPermissions: (
      state,
      action: PayloadAction<{ user: User.Profile; shouldResetSubmenu?: boolean }>
    ) => {
      const { userRole } = action.payload.user;
      const filteredAdvisorNav = filterNavItems(state.advisorNav, userRole);
      const filteredClientNav = filterNavItems(state.clientNav, userRole);
      state.advisorNav = filteredAdvisorNav;
      state.clientNav = filteredClientNav;
      if (action.payload.shouldResetSubmenu) state.activeSubmenu = initialState.activeSubmenu;
    }
  }
});

export const { setActiveSubmenu, setNavPermissions } = gnbSlice.actions;

export default gnbSlice.reducer;
