import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import _findIndex from 'lodash/findIndex';

// ----------------------------------------------------------------------
export type CreateCustomerState = {
  formData: CreateCustomer.CustomerFormData[];
};
const initialState: CreateCustomerState = { formData: [] };

export const createCustomerSlice = createSlice({
  name: 'customer',
  initialState,
  reducers: {
    saveCreateCustomerData: (
      state,
      action: PayloadAction<{ data: CreateCustomer.CustomerFormData }>
    ) => {
      const createData = action.payload.data;

      const index = _findIndex(state.formData, (data: CreateCustomer.CustomerFormData) => {
        return data.basicInfo.basicList.name === createData.basicInfo.basicList.name;
      });
      if (index >= 0) state.formData[index] = createData;
      else {
        state.formData = [...state.formData, createData];
      }
    }
  }
});

export const { saveCreateCustomerData } = createCustomerSlice.actions;

export default createCustomerSlice.reducer;
