import { combineReducers, CombinedState } from 'redux';
import locale, { LocaleState } from './slices/locale/localeSlice';
import auth, { AuthState } from './slices/auth/authSlice';
import gnb, { GnbState } from './slices/gnb/gnbSlice';
import customer, { CreateCustomerState } from './slices/customer/createSlice';
// ---------------------------------------------------------------------- //

const rootReducer = combineReducers({
  locale,
  auth,
  gnb,
  customer
});

export type RootState = CombinedState<{
  locale: CombinedState<LocaleState>;
  auth: CombinedState<AuthState>;
  gnb: CombinedState<GnbState>;
  customer: CombinedState<CreateCustomerState>;
}>;

export type RootReducerType = ReturnType<typeof rootReducer>;

export default rootReducer;
