import * as Yup from 'yup';
// ----------------------------------------------------------------------

export const REG_EXP = {
  email:
    // eslint-disable-next-line no-useless-escape
    /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
  phone: /^(01[016789])-?[0-9]{3,4}-?[0-9]{4}$/
};

// Extend Yup with custom validation methods
Yup.addMethod<Yup.StringSchema>(Yup.string, 'emailV2', function (message: string) {
  return this.matches(REG_EXP.email, {
    message,
    excludeEmptyString: true
  });
});

declare module 'yup' {
  interface StringSchema {
    emailV2(message: string): StringSchema;
  }
}

export default Yup;
