import { createContext, useEffect, useReducer } from 'react';
import { logger } from 'utils';
import { setSession, getAccessToken } from 'utils/jwtUtils';
import { useNavigate } from 'react-router-dom';
import { signInApi, getUserProfileApi } from 'api/auth';
import _get from 'lodash/get';
import { PATH_AUTH } from 'routes/paths';
// import { AUTH_ROLES } from 'config/auth';
import { useDispatch } from 'store';
import { setNavPermissions } from 'store/slices/gnb';
import { User } from '@package/shared-consts';

// ----------------------------------------------------------------------

enum Types {
  INITIAL = 'INITIAL',
  LOGIN = 'LOGIN',
  LOGOUT = 'LOGOUT'
}

type Payload = {
  [Types.INITIAL]: {
    isAuthenticated: boolean;
    user: User.Profile | null;
  };
  [Types.LOGIN]: {
    user: User.Profile | null;
  };
  [Types.LOGOUT]: undefined;
};

type JWTContextType = {
  isAuthenticated: boolean;
  isInitialized: boolean;
  user: User.Profile | null;
  login: (
    email: string,
    password: string
  ) => Promise<{ success: boolean; errors?: Record<string, string> }>;
  logout: (shouldRedirectToLogin: boolean) => void;
};

type AuthStateType = {
  isAuthenticated: boolean;
  isInitialized: boolean;
  user: User.Profile | null;
};

// eslint-disable-next-line @typescript-eslint/no-explicit-any
type ActionMapType<M extends { [index: string]: any }> = {
  [Key in keyof M]: M[Key] extends undefined
    ? {
        type: Key;
      }
    : {
        type: Key;
        payload: M[Key];
      };
};

type ActionsType = ActionMapType<Payload>[keyof ActionMapType<Payload>];

// ----------------------------------------------------------------------

const initialState: AuthStateType = {
  isInitialized: false,
  isAuthenticated: false,
  user: null
};

const reducer = (state: AuthStateType, action: ActionsType) => {
  switch (action.type) {
    case Types.INITIAL:
      return {
        isInitialized: true,
        isAuthenticated: action.payload.isAuthenticated,
        user: action.payload.user
      };
    case Types.LOGIN:
      return {
        ...state,
        isAuthenticated: true,
        user: action.payload.user
      };
    case Types.LOGOUT:
      return {
        ...state,
        isAuthenticated: false,
        user: null
      };
    default:
      return state;
  }
};

// ----------------------------------------------------------------------

export const AuthContext = createContext<JWTContextType | null>(null);

// ----------------------------------------------------------------------

type AuthProviderProps = {
  children: React.ReactNode;
};

export function AuthProvider({ children }: AuthProviderProps) {
  const [state, dispatch] = useReducer(reducer, initialState);
  const navigate = useNavigate();
  const reduxDispatch = useDispatch();

  useEffect(() => {
    (async () => {
      logger.debug('@@@ JWTContext INIT @@@');
      const accessToken = getAccessToken();
      if (accessToken) {
        setSession(accessToken);
        const user = await getUserProfileApi();
        // user.userRole = AUTH_ROLES.FA_ADMIN;
        reduxDispatch(setNavPermissions({ user, shouldResetSubmenu: true }));

        dispatch({
          type: Types.INITIAL,
          payload: {
            isAuthenticated: user ? true : false,
            user: user ? user : null
          }
        });
      } else {
        dispatch({
          type: Types.INITIAL,
          payload: { isAuthenticated: false, user: null }
        });
      }
    })().catch((err) => {
      logger.error('APP initialize [ERROR]', err);
      dispatch({
        type: Types.INITIAL,
        payload: { isAuthenticated: false, user: null }
      });
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  // LOGIN
  const login = async (
    username: string,
    password: string
  ): Promise<{ success: boolean; errors?: Record<string, string> }> => {
    try {
      const accessToken = await signInApi(username, password);

      if (!accessToken) return { success: false };

      setSession(accessToken);

      const user = await getUserProfileApi();
      if (!user) return { success: false };
      // user.userRole = AUTH_ROLES.FA_ADMIN;
      reduxDispatch(setNavPermissions({ user, shouldResetSubmenu: true }));

      dispatch({ type: Types.LOGIN, payload: { user } });
      return { success: true };
    } catch (error) {
      const errorMsg = _get(error, 'response.data.message', '오류가 발생했습니다') as string;
      logger.error('[login] ', errorMsg, error);
      return {
        success: false,
        errors: { password: errorMsg }
      };
    }
  };

  // LOGOUT
  const logout = (shouldRedirectToLogin: boolean) => {
    setSession(null);
    dispatch({ type: Types.LOGOUT });
    if (shouldRedirectToLogin) navigate(PATH_AUTH.login);
  };

  return (
    <AuthContext.Provider
      value={{
        ...state,
        login,
        logout
      }}
    >
      {children}
    </AuthContext.Provider>
  );
}
